  -webkit-box-shadow: 0 0 0 1000px white inset;
}
.checkbox {
  display: inline-block;
  height: 3rem;
  position: relative;
  vertical-align: middle;
  width: 44px;
}
.checkbox input[type='checkbox'] {
  font-size: 1em;
  visibility: hidden;
}
.checkbox input[type='checkbox'] + span:before {
  position: absolute;
  top: 50%;
  right: auto;
  bottom: auto;
  left: 50%;
  width: 0.85em;
  height: 0.85em;
  transform: translate3d(-50%, -50%, 0);
  background: transparent;
  box-shadow: #2cc5d2 0 0 0 1px inset;
  content: '';
  display: block;
}
.checkbox input[type='checkbox']:checked + span:before {
  font-size: 16px;
  line-height: 24px;
  box-shadow: none;
  color: #2cc5d2;
  margin-top: -1px;
  font-family: 'percolate';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: '\e65e';
}
.input-symbol {
  display: inline-block;
  position: relative;
}
.input-symbol.error [class^='icon-'],
.input-symbol.error [class*=' icon-'] {
  color: #ff4400;
}
.input-symbol [class^='icon-'],
.input-symbol [class*=' icon-'] {
  left: 1em;
}
.input-symbol input {
  padding-left: 3em;
}
.input-symbol input {
  width: 100%;
}
.input-symbol input:focus + [class^='icon-'],
.input-symbol input:focus + [class*=' icon-'] {
  color: #2cc5d2;
}
.input-symbol [class^='icon-'],
.input-symbol [class*=' icon-'] {
  transition: all 300ms ease-in;
  transform: translate3d(0, -50%, 0);
  background: transparent;
  color: #aaa;
  font-size: 1em;
  height: 1em;
  position: absolute;
  top: 50%;
  width: 1em;
}
